import React from 'react'

import ReactEcharts from 'echarts-for-react';

class Count extends React.Component {

    getOtion = () => {
        let x = Object.keys(this.props.mcount)
        let gcount = [], mcount = [], lcount = [];
        x = x.filter(item => {
            let _item = parseInt(item);
            let cTime = new Date().getMonth() + 1;
            if (cTime === 2) { //如果当前是2月
                return _item === 2 || _item === 1 || _item === 12
            } else if (cTime === 1) { //如果当前是1月
                return _item === 1 || _item === 12 || _item === 11
            } else if (cTime !== 1 && cTime !== 2) {
                return _item === cTime  || _item === cTime - 1 || _item === cTime -2
            }
            return false
        })
        x.forEach(item => {
            gcount.push(parseFloat(this.props.gcount[item]).toFixed(2))
            lcount.push(parseFloat(this.props.lcount[item]).toFixed(2))
            mcount.push(parseFloat(this.props.mcount[item]).toFixed(2))
        })
        x = x.map(item => (
            item + '月'
        ))
        return {
            tooltip: {
                trigger: 'axis'
            },
            calculable: true,
            xAxis: {
                type: 'category',
                data: x,
            },
            legend: {
                data: ['长途话费', '本地话费', '短信话费']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '长途话费',
                    stack: '总量',
                    data: gcount,
                    type: 'line'
                },
                {
                    name: '本地话费',
                    stack: '总量',
                    data: lcount,
                    type: 'line'
                },
                {
                    name: '短信话费',
                    stack: '总量',
                    data: mcount,
                    type: 'line'
                }
            ]
        }
    }

    render() {
        return <>
            <ReactEcharts
                option={this.getOtion()}
                style={{ height: '400px', width: '500px' }}
            />
        </>
    }
}
export default Count;