import React from 'react'
import { Upload, Button, Modal, Input, message, Table, Popconfirm, Icon } from 'antd';
import SelfTable from '../component/table';
import './form.css'
import ButtonGroup from 'antd/lib/button/button-group';
const Search = Input.Search;

class Form extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            user: null,
            currentRole: null,
            userList: [],
            editUser: false,
            editUserList: false,
            editPhone: '',
            userTable: [],
            setMoney: false,
            currentMoney: {
                global: 0,
                local: 0,
                message: 0
            },
            searchData: {}
        }
    }

    handleOk = () => {
        if (this.state.currentRole.phone === this.state.editPhone){
            this.setState({
                editUser: false
            })
            return;
        }
        fetch('http://127.0.0.1:5001/editUser', {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                user: this.state.currentRole.user,
                phone: this.state.editPhone
            })
        }).then(res => {
            res.json().then(data => {
                if (data.code === 200) {
                    message.success('更新成功', 0.5)
                    if (data.data.user === this.state.user.user) {
                        sessionStorage.setItem('premission', JSON.stringify(data.data))
                        this.setState({
                            user: data.data,
                            editUser: false,
                            searchData: {}
                        })
                    } else {
                        this.setState({
                            editUser: false
                        })
                        this.handleEditUserList()
                    }
                } else {
                    message.error(data.message, 0.5)
                }
            })
        })
    }

    handleCancel = (e) => {
        this.setState({
            editUser: false,
            editUserList: false,
            setMoney: false
        });
    }


    componentWillMount() {
        let data = sessionStorage.getItem('premission');
        if (data.length > 0) {
            data = JSON.parse(data);
            this.setState({
                user: data
            });
        };

    }

    handleEditMessage(role) {
        this.setState({
            editUser: true,
            editUserList: false,
            currentRole: role,
            editPhone: role.phone
        })
    }

    onChangeForm(e, type) {
        switch (type) {
            case 'edit':
                this.setState({
                    editPhone: e.target.value
                })
                break;
            case 'global':
                this.setState({
                    currentMoney: Object.assign(this.state.currentMoney, { global: e.target.value })
                })
                break;
            case 'local':
                this.setState({
                    currentMoney: Object.assign(this.state.currentMoney, { local: e.target.value })
                })
                break;
            case 'message':
                this.setState({
                    currentMoney: Object.assign(this.state.currentMoney, { message: e.target.value })
                })
                break;
            default:
                return;
        }

    }

    handleEditUserList = () => {
        fetch('http://127.0.0.1:5001/getUser').then(res => {
            res.json().then(data => {
                this.setState({
                    editUserList: true,
                    userList: data
                })
            })
        })
    }

    handleDeleteRole = (value) => {
        if (value.user === this.state.user.user) {
            message.error('无法删除你自身账号', 0.5);
        } else {
            fetch('http://127.0.0.1:5001/deleteUser', {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    user: value.user
                })
            }).then(res => {
                res.json().then(data => {
                    if (data.code === 200) {
                        this.handleEditUserList()
                    }
                })
            })
        }
    }

    handleLogout = () => {
        sessionStorage.removeItem('premission')
        this.props.history.replace('/')
    }

    handleSetMoneny = () => {
        fetch('http://127.0.0.1:5001/getMoney').then(res => {
            res.json().then(data => {
                let _data = data.data
                if (!_data) {
                    _data = {
                        global: 0,
                        local: 0,
                        message: 0
                    }
                }

                this.setState({
                    setMoney: true,
                    currentMoney: _data
                })
            })
        })
    }

    editMoney = () => {
        let { global, local } = this.state.currentMoney;
        let _message = this.state.currentMoney.message;
        fetch('http://127.0.0.1:5001/setMoney', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                global: global * 1,
                local: local * 1,
                message: _message * 1
            })
        }).then(res => {
            res.json().then(data => {
                if (data.code === 200) {
                    message.success('话费设定成功', 0.5)
                    this.setState({
                        setMoney: false
                    })
                }
            })
        })

    }

    handleUpload = (file) => {
        let ext = file.name.substring(file.name.lastIndexOf('.') + 1);
        if (ext.toLowerCase() === 'json' || ext.toLowerCase() === 'txt') {
            let reader = new FileReader(), fileString = '';
            reader.readAsText(file, "UTF-8");//读取文件 
            reader.onload = function (evt) { //读取完文件之后会回来这里
                fileString = evt.target.result; // 读取文件内容
                try {
                    JSON.parse(fileString);
                    fetch('http://127.0.0.1:5001/upload', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: fileString
                    }).then(res => {
                        res.json().then(data => {
                            if (data.code === 200) {
                                message.success('上传成功', 0.5)
                            }
                        })
                    })
                } catch {
                    message.error('请确保JSON格式正确', 0.5)
                }
            }
        } else {
            message.error('请上传JSON或TXT文本文件', 0.5)
        }
        return false;
    }

    onlyData = (value) => {
        let tag = '', result = [], obj = {};
        for (let i = 0; i < value.length; i++) {
            tag = value[i].start + '' + value[i].end;
            if (!obj[tag]) {
                result.push(value[i]);
                obj[tag] = true;
            }
        }
        return result
    }


    handleSearchRecord = (value) => {
        let _data = null;
        fetch('http://127.0.0.1:5001/getMoney').then(res => {
            res.json().then(data => {
                _data = data.data
                if (!_data) {
                    _data = {
                        global: 0,
                        local: 0,
                        message: 0
                    }
                }
                fetch(`http://127.0.0.1:5001/record?number=${value}`).then(res => {
                    res.json().then(data => {
                        if (data.code === 200) {
                            if (data.data && data.data.length === 0) {
                                message.info('暂无通信记录')
                                this.setState({
                                    searchData: {}
                                })
                            } else {
                                let onlyData = data.data;
                                onlyData.globalCall = this.onlyData(onlyData.globalCall)
                                onlyData.localCall = this.onlyData(onlyData.localCall)
                                onlyData.message = [...new Set(onlyData.message)]
                                this.setState({
                                    searchData: data.data,
                                    currentMoney: _data
                                })
                            }
                        } else {
                            message.error('查询出错')
                        }
                    })
                })
            })
        })

    }

    render() {
        const columns = [{
            title: '账户名',
            dataIndex: 'user',
            key: 'user',
        }, {
            title: '手机号',
            dataIndex: 'phone',
            key: 'phone',
        }, {
            title: '权限',
            dataIndex: 'premission',
            key: 'premission',
        }, {
            title: '管理',
            key: 'action',
            render: (value) => {
                return <ButtonGroup>
                    <Button size="small" onClick={() => this.handleEditMessage(value)}>编辑</Button>
                    <Popconfirm title="确定删除吗？" okText="确定" cancelText="取消" onConfirm={() => this.handleDeleteRole(value)}>
                        <Button size="small" type="danger">删除</Button>
                    </Popconfirm>
                </ButtonGroup>
            },
        }];


        return (
            <div className="formWrap">
                <div className="header">
                    <h1>欢迎使用电信计费与查询系统</h1>
                    <ButtonGroup>
                        <Button type="primary" onClick={() => this.handleEditMessage(this.state.user)}>本账户信息编辑</Button>
                        <Button onClick={() => this.handleLogout()}>登出</Button>
                    </ButtonGroup>

                </div>
                <hr />
                {
                    (this.state.user !== null && this.state.user.premission === 'admin') ? <div className="buttonGroup">
                        <Button type='primary' onClick={this.handleSetMoneny}>费率设定</Button>
                        <Button type="primary" onClick={this.handleEditUserList}>用户管理</Button>
                        <Upload
                            beforeUpload={(file) => this.handleUpload(file)}
                            showUploadList={false}
                        >
                            <Button type="primary">
                                <Icon type="upload" /> 话单上传
                            </Button>
                        </Upload>
                    </div> : null
                }

                <div>
                    {
                        this.state.user.premission === 'admin' ? <Search
                            placeholder="请输入要查询的电话号码"
                            onSearch={value => this.handleSearchRecord(value)}
                            size="large"
                            enterButton
                            type="number"
                        /> : <Button onClick={() => this.handleSearchRecord(this.state.user.phone)}>查询本人账单</Button>
                    }
                    {
                        JSON.stringify(this.state.searchData) !== '{}' &&
                        <SelfTable
                            data={this.state.searchData}
                            money={this.state.currentMoney}
                        />
                    }

                </div>

                {/* 下面是隐藏的编辑弹窗 */}
                <Modal
                    title="本人信息编辑"
                    visible={this.state.editUser}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    okText="确认"
                    cancelText="关闭"
                >
                    {
                        this.state.currentRole && <>
                            <p>
                                账户名称: {this.state.currentRole.user}
                            </p>
                            <p>
                                您的权限: {this.state.currentRole.premission === 'admin' ? '管理员' : '普通用户'}
                            </p>
                            <p className="inputGroup">
                                <span>您的电话: </span>
                                <Input value={this.state.editPhone} onChange={(e) => this.onChangeForm(e, 'edit')} />
                            </p>
                        </>}
                </Modal>


                <Modal
                    title="用户信息管理"
                    visible={this.state.editUserList}
                    onOk={this.handleCancel}
                    onCancel={this.handleCancel}
                    okText="确认"
                    cancelText="关闭"
                >
                    <Table dataSource={this.state.userList} columns={columns} />
                </Modal>


                {this.state.currentMoney && <Modal
                    title="费率设定"
                    visible={this.state.setMoney}
                    onOk={this.editMoney}
                    onCancel={this.handleCancel}
                    okText="确认"
                    cancelText="关闭"
                >
                    <div className="setMoney">
                        <p className="inputGroup">
                            <span>长途话费: </span>
                            <Input type="number" value={this.state.currentMoney.global} onChange={(e) => this.onChangeForm(e, 'global')} />
                        </p>
                        <p className="inputGroup">
                            <span>本地话费: </span>
                            <Input type="number" value={this.state.currentMoney.local} onChange={(e) => this.onChangeForm(e, 'local')} />
                        </p>
                        <p className="inputGroup">
                            <span>短信话费: </span>
                            <Input type="number" value={this.state.currentMoney.message} onChange={(e) => this.onChangeForm(e, 'message')} />
                        </p>
                    </div>

                </Modal>}


            </div>
        )
    }
}

export default Form;