import React, { Component } from 'react';
import { Input, Icon, Button,message } from 'antd';
import './login.css'

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            usrPass: '',
            confirmPass: '',
            showRegister: false
        };
    }

    componentDidMount() {
        let data = sessionStorage.getItem('premission');
        if (data && data.length > 0) {
            this.props.history.replace('/form')            
        }
    }

    emitEmptyName = () => {
        this.userNameInput.focus();
        this.setState({ userName: '' });
    }

    emitEmptyPass = () => {
        this.userPassInput.focus();
        this.setState({ usrPass: '' });
    }

    emitEmptyPassConfirm = () => {
        this.userPassConfirm.focus();
        this.setState({ confirmPass: '' });
    }

    onChangeForm = (e, type) => {
        switch (type) {
            case 'user':
                this.setState({ userName: e.target.value });
                break;
            case 'pass':
                this.setState({ usrPass: e.target.value });
                break;
            case 'passconfirm':
                this.setState({ confirmPass: e.target.value });
                break;
            default:
                return;
        }
    }

    handleSendServer = (type) => { //'r'是注册，'l'是登陆
        let {userName,usrPass,confirmPass} = this.state;
        // 发起登陆请求
        if (type === 'r') {
            if (userName === '' || usrPass === '' || confirmPass === ''){
                message.warning('请确认所有信息已填写',0.5);
                return;
            }
            if (confirmPass !== usrPass) {
                message.warning('两次密码不一致，请确认',0.5);
                return;
            }
            // console.log(userName,usrPass,confirmPass)
        }else {
            if (userName === '' || usrPass === ''){
                message.warning('请确认所有信息已填写',0.5);
                return;
            }
        }
        fetch('http://127.0.0.1:5001/login',{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                user: userName,
                pass: usrPass,
                type: type
            })
        }).then(res => {
            res.json().then(data => {
                if (data.code !== 200) {
                    message.error(data.message,0.5)
                }else{
                    sessionStorage.setItem('premission', JSON.stringify(data.data))
                    this.props.history.push('/form')
                }
            })
        })
    }

    handleJudgeRegister = (flag = false) => {
        this.setState({
            userName: '',
            usrPass: '',
            confirmPass: '',
            showRegister: flag
        })
    }


    render() {
        const { userName, usrPass,confirmPass } = this.state;
        const suffixName = userName ? <Icon type="close-circle" onClick={this.emitEmptyName} /> : null;
        const suffixPass = usrPass ? <Icon type="close-circle" onClick={this.emitEmptyPass} /> : null;
        const suffixPassConfirm = confirmPass ? <Icon type="close-circle" onClick={this.emitEmptyPassConfirm} /> : null;

        return (
            <div className="login">
                <h2>电信计费与查询系统</h2>
                <div className="loginForm">
                    <Input
                        placeholder="请输入用户名"
                        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                        suffix={suffixName}
                        value={userName}
                        onChange={(e) => this.onChangeForm(e, 'user')}
                        ref={node => this.userNameInput = node}
                    />
                    <Input
                        placeholder="密码"
                        type="password"
                        prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                        suffix={suffixPass}
                        value={usrPass}
                        onChange={(e) => this.onChangeForm(e, 'pass')}
                        ref={node => this.userPassInput = node}
                    />
                    {
                        this.state.showRegister && <Input
                            placeholder="确认密码"
                            type="password"
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            suffix={suffixPassConfirm}
                            value={confirmPass}
                            onChange={(e) => this.onChangeForm(e, 'passconfirm')}
                            ref={node => this.userPassConfirm = node}
                        />
                    }
                    {
                        this.state.showRegister ? <div className="buttonGroup">
                            <Button type="primary" onClick={() => this.handleJudgeRegister(false)}>返回登陆</Button>
                            <Button onClick={() => this.handleSendServer('r')}>立即注册</Button>
                        </div>
                            : <div className="buttonGroup">
                                <Button type="primary" onClick={() => this.handleSendServer('l')}>登陆</Button>
                                <Button onClick={() => this.handleJudgeRegister(true)}>注册</Button>
                            </div>
                    }
                </div>

            </div>
        );
    }
}

export default Login;