import React from 'react'
import { Table, Button,Modal } from 'antd'
import './table.css'
import Count from './count'
const { Column, ColumnGroup } = Table;
let gcount = {} //只算近3个月的
let lcount = {}
let mcount = {}
class SelfTable extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            showPic: false
        }
    }

    timeDiff(nTime) {
        if (typeof nTime !== 'number') {
            return '-'
        }
        let runtime = parseInt(nTime / 1000);
        let day = Math.floor(runtime / 60 / 60 / 24);
        runtime %= 86400;
        let hour = Math.floor(runtime / 60 / 60);
        runtime %= 3600;
        let minute = Math.floor(runtime / 60);
        runtime %= 60;
        let second = runtime;
        if (day > 0) {
            return `${day}天${hour}小时${minute}分${second}秒`;
        } else if (day === 0 && hour > 0) {
            return `${hour}小时${minute}分${second}秒`;
        } else if (day === 0 && hour === 0 && minute > 0) {
            return `${minute}分${second}秒`;
        } else if (day === 0 && hour === 0 && minute === 0 && second) {
            return `${second}秒`;
        }
    }

    completeMoney(time, money) {
        if (Math.floor(time / 1000 / 60) === 0) { //小于1分钟,以1分钟算
            return money
        } else {
            return money * Math.floor(time / 1000 / 60)
        }
    }

    formmatData = (data, money) => {
        if (JSON.stringify(data) === '{}') {
            return []
        }
        if (!money) {
            return []
        }
        let { global, local, message } = money;
        let cTime = new Date().getMonth() + 1;
        if (cTime === 2) { //如果当前是2月
            gcount[1] = 0;
            gcount[2] = 0;
            gcount[12] = 0; //去年12月

            lcount[1] = 0;
            lcount[2] = 0;
            lcount[12] = 0;


            mcount[1] = 0;
            mcount[2] = 0;
            mcount[12] = 0;

        } else if (cTime === 1) { //如果当前是1月
            gcount[1] = 0;
            gcount[11] = 0;
            gcount[12] = 0;

            lcount[1] = 0;
            lcount[11] = 0;
            lcount[12] = 0;

            mcount[1] = 0;
            mcount[11] = 0;
            mcount[12] = 0;

        } else if(cTime !== 1 && cTime !== 2 ) {
            gcount[cTime] = 0;
            gcount[cTime - 1] = 0;
            gcount[cTime - 2] = 0;

            lcount[cTime] = 0;
            lcount[cTime - 1] = 0;
            lcount[cTime - 2] = 0;

            mcount[cTime] = 0;
            mcount[cTime - 1] = 0;
            mcount[cTime - 2] = 0;
        }
        
        let _data = [], maxLength = Math.max(data.globalCall.length, data.localCall.length, data.message.length);
        let _time = null;
        for (let i = 0; i < maxLength; i++) {
            let item = {};
            item['key'] = i;
            item['globalStartTime'] = !data.globalCall[i] ? '-' : data.globalCall[i].start;
            item['globalEndTime'] = !data.globalCall[i] ? '-' : data.globalCall[i].end;
            item['globalTotalTime'] = !data.globalCall[i] ? '-' : data.globalCall[i].end - data.globalCall[i].start;
            item['globalMoney'] = !data.globalCall[i] ? '-' : this.completeMoney(data.globalCall[i].end - data.globalCall[i].start, global) + '元'
            item['localStartTime'] = !data.localCall[i] ? '-' : data.localCall[i].start;
            item['localEndTime'] = !data.localCall[i] ? '-' : data.localCall[i].end;
            item['localTotalTime'] = !data.localCall[i] ? '-' : data.localCall[i].end - data.localCall[i].start;
            item['localMoney'] = !data.localCall[i] ? '-' : this.completeMoney(data.localCall[i].end - data.localCall[i].start, local) + '元'
            item['message'] = !data.message[i] ? '-' : data.message[i]
            item['messageMoney'] = !data.message[i] ? '-' : message + '元'
            _data.push(item)
            if (data.globalCall[i]) {
                _time = new Date(data.globalCall[i].start)
                if (gcount[_time.getMonth() + 1] !== null) {
                    gcount[_time.getMonth() + 1] += this.completeMoney(data.globalCall[i].end - data.globalCall[i].start, global)
                }
            }

            if (data.localCall[i]) {
                _time = new Date(data.localCall[i].start)
                if (lcount[_time.getMonth() + 1] !== null) {
                    lcount[_time.getMonth() + 1] += this.completeMoney(data.localCall[i].end - data.localCall[i].start, local)
                }
            }

            if (data.message[i]) {
                _time = new Date(data.message[i])
                if (mcount[_time.getMonth() + 1] !== null ||mcount[_time.getMonth() + 1 !== undefined]) {
                    mcount[_time.getMonth() + 1] += message;
                }else{
                    delete(mcount[10])
                } 
            }
            
        }
        return _data;
    }

    handleSort(a, b, type) {
        switch (type) {
            case 'globalStart':
                return a.globalStartTime - b.globalStartTime;
            case 'globalEnd':
                return a.globalEndTime - b.globalEndTime;
            case 'globalTotal':
                return a.globalTotalTime - b.globalTotalTime;
            case 'localStart':
                return a.localStartTime - b.localStartTime;
            case 'localEnd':
                return a.localEndTime - b.localEndTime;
            case 'localTotal':
                return a.localTotalTime - b.localTotalTime;
            case 'message':
                return a.message - b.message
            default:
                return;
        }
    }

    handleUtilTime(time) {
        if (time === '-') {
            return '-'
        } else {
            let _time = new Date(time);
            return _time.toLocaleString()
        }

    }

    handleCancel = () =>{
        this.setState({
           showPic : false
        })
    }
    handlePic = () => {
        this.setState({
            showPic : true
        })
    }

    render() {
        let data = this.formmatData(this.props.data, this.props.money);
        return (
            <>
                {
                    (JSON.stringify(this.props.money) !== '{}' && this.props.money )&&
                    <>
                        <h3 className="totalMoneySetting">
                            当前费率： 长途{this.props.money.global}元/分 | 本地{this.props.money.local}元/分 |  短信{this.props.money.message}元/条
                        <Button onClick={this.handlePic}>折线图</Button>
                        </h3>
                    </>
                }
                <Table
                    title={() => {
                        if (JSON.stringify(this.props.data) !== '{}') {
                            return `用户：${this.props.data.username} 号码：${this.props.data.phone}`
                        } else {
                            return '暂无此用户'
                        }
                    }}
                    className="selfTable"
                    bordered={true}
                    locale={{
                        emptyText: '暂无数据',
                    }}
                    dataSource={data}
                >
                    <ColumnGroup title="长途记录">
                        <Column
                            title="起始时间"
                            dataIndex="globalStartTime"
                            key="globalStartTime"
                            sorter={(a, b) => this.handleSort(a, b, 'globalStart')}
                            render={(value) => this.handleUtilTime(value)}
                        />
                        <Column
                            title="结束时间"
                            dataIndex="globalEndTime"
                            key="globalEndTime"
                            sorter={(a, b) => this.handleSort(a, b, 'globalEnd')}
                            render={(value) => this.handleUtilTime(value)}
                        />
                        <Column
                            title="共计时间"
                            dataIndex="globalTotalTime"
                            key="globalTotalTime"
                            sorter={(a, b) => this.handleSort(a, b, 'globalTotal')}
                            render={(value) => this.timeDiff(value)}
                        />
                        <Column
                            title="费用"
                            dataIndex="globalMoney"
                            key="globalMoney"
                        />
                    </ColumnGroup>


                    <ColumnGroup title="本地记录">
                        <Column
                            title="起始时间"
                            dataIndex="localStartTime"
                            key="localStartTime"
                            sorter={(a, b) => this.handleSort(a, b, 'localStart')}
                            render={(value) => this.handleUtilTime(value)}
                        />
                        <Column
                            title="结束时间"
                            dataIndex="localEndTime"
                            key="localEndTime"
                            render={(value) => this.handleUtilTime(value)}
                            sorter={(a, b) => this.handleSort(a, b, 'localEnd')}
                        />
                        <Column
                            title="共计时间"
                            dataIndex="localTotalTime"
                            render={(value) => this.timeDiff(value)}
                            key="localTotalTime"
                            sorter={(a, b) => this.handleSort(a, b, 'localTotal')}
                        />
                        <Column
                            title="费用"
                            dataIndex="localMoney"
                            key="localMoney"
                        />
                    </ColumnGroup>

                    <Column
                        title="短信记录"
                        dataIndex="message"
                        key="message"
                        render={(value) => this.handleUtilTime(value)}
                        sorter={(a, b) => this.handleSort(a, b, 'message')}
                    />
                    <Column
                        title="费用"
                        dataIndex="messageMoney"
                        key="messageMoney"
                    />
                </Table>


                <Modal
                    title="近三月费用折线图"
                    visible={this.state.showPic}
                    onOk={this.handleCancel}
                    onCancel={this.handleCancel}
                    okText='确认'
                    cancelText='关闭'
                >
                    <Count 
                        mcount={mcount}
                        lcount={lcount}
                        gcount={gcount}
                    />
                </Modal>
            </>
        )
    }
}

export default SelfTable;